import * as React from 'react'
import { graphql } from 'gatsby'
import { Remarkable } from 'remarkable'
import Layout from './../layout/index'
import { SEO, StripeBar, Banner } from './../components'
import {
  Eyes,
  Content,
  GenderDiversity,
  Sources,
} from './../containers/Traditional'
import Image2 from './../images/traditional-knowledge-eyes.svg'
import Image3 from './../images/traditional-knowledge-two-spirit-figures-light.svg'

interface TraditionalPageProps {
  location: {
    pathname: string
  }
  pageContext: {
    title: string
    section11: string
    section12: string
    section2: string
    section31: string
    section32: string
    section331: string
    section332: string
    sources: string
    seoKeyword: string
    seoDescription: string
    section31Terms: {
      description: string
      title: string
    }[]
  }
}

export default ({
  location,
  pageContext: {
    title,
    section11,
    section12,
    section2,
    section31,
    section32,
    section331,
    section332,
    sources,
    section31Terms,
    seoDescription,
    seoKeyword,
  },
}: TraditionalPageProps) => {
  const md = new Remarkable()
  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        description={seoDescription}
        keyword={seoKeyword}
        title={title}
      />
      <Banner>
        <h1>{title}</h1>
      </Banner>
      <StripeBar colors={['#00587F', '#80D0DB', '#5A9DB3', '#95C5BB']} />
      <Content part1={section11} part2={section12} />
      <Eyes image={Image2}>
        <p>{section2}</p>
      </Eyes>
      <StripeBar colors={['#80D0DB', '#95C5BB', '#E58630', '#DA5027']} />
      <GenderDiversity
        firstNation={section31}
        firstNationTerms={section31Terms}
        inuit={section32}
        metis1={section331}
        metis2={section332}
      />
      <Sources content={md.render(sources)} />
    </Layout>
  )
}

export const traditionalPageQuery = graphql`
  query TradtiionalPageQuery {
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`
