import React from 'react'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'
import { Box, Accordion, TextContent } from './../../../components'
import { Container } from './style'
import styled from 'styled-components'
interface IProps {
  firstNation: string
  firstNationTerms: any
  inuit: string
  metis1: string
  metis2: string
}

export default ({
  firstNation,
  firstNationTerms,
  inuit,
  metis1,
  metis2,
}: IProps) => {
  const md = new Remarkable()

  return (
    <Container>
      <div className='container'>
        <div className='d-flex flex-wrap'>
          <div className='row'>
            <div className='col-md-6'>
              <Box className='h-100'>
                {ReactHTMLParser(md.render(firstNation))}
                <Accordion
                  className='mt-5'
                  data={firstNationTerms.map((d: any) => {
                    return {
                      ...d,
                      content: d.description,
                    }
                  })}
                />
              </Box>
            </div>
            <div className='col-md-6 mt-4 mt-md-0'>
              <Box>{ReactHTMLParser(md.render(inuit))}</Box>
            </div>
            <div className='col-md-12 mt-4'>
              <Box>
                <h3 className='font-weight-bold'>Métis</h3>
                <div className='d-flex flex-wrap'>
                  <div className='row'>
                    <div className='col-md-6'>
                      <div>{ReactHTMLParser(md.render(metis1))}</div>
                    </div>
                    <div className='col-md-6'>
                      <Content>
                        <div>{ReactHTMLParser(md.render(metis2))}</div>
                      </Content>
                    </div>
                  </div>
                </div>
              </Box>
            </div>
          </div>
        </div>
      </div>
    </Container>
  )
}

const Content = styled.div`
  font-size: 22px;
  line-height: 34px;
  color: ${props => props.theme.palette.primary};
  a {
    font-size: 22px;
    line-height: 32px;
    color: ${props => props.theme.palette.darkSecondary};
    position: relative;
    text-decoration: none;
    display: inline-block;

    &:before {
      content: '';
      position: absolute;
      width: 0%;
      height: 4px;
      bottom: 0px;
      left: 0;
      background-color: ${props => props.theme.palette.darkSecondary};
      visibility: hidden;
      transition: all 0.5s ease-in-out 0s;
    }
    &:hover::before {
      width: 100%;
      visibility: visible;
    }
    &::hover {
      color: ${props => props.theme.palette.darkSecondary};
      text-decoration: none;
    }
  }
`
