import React from 'react'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'
import Image1 from './../../../images/traditional-knowledge-two-spirit-figures-dark.svg'

import { Container } from './style'

interface IContentProps {
  part1: string
  part2: string
}

export default ({ part1, part2 }: IContentProps) => {
  const md = new Remarkable()

  return (
    <Container>
      <div className='container'>
        <div className='row'>
          <div className='col-md-6'>
            <img src={Image1} alt='two spirit' />
            <div className='mt-3'>{ReactHTMLParser(md.render(part1))}</div>
          </div>
          <div className='col-md-6'>
            <div className='mt-5'>{ReactHTMLParser(md.render(part2))}</div>
          </div>
        </div>
      </div>
    </Container>
  )
}
