import React from 'react'
import { useLocation } from '@reach/router'

import Accordion from './../../../containers/Ask/FAQAccordion'
import BackImg from './../../../images/traditional-knowledge-two-spirit-figures-light.svg'
import { Container } from './style'

interface IProps {
  content: string
}

export default ({ content }: IProps) => {
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')
  return (
    <Container className='py-5'>
      <div className='container'>
        <div className='d-flex flex-wrap'>
          <div className='col-md-6 flex-column d-flex'>
            <Accordion
              className='my-auto pt-5'
              data={[
                {
                  title: isFr
                    ? 'Ces sources ont été utilisées pour la création de cette page web'
                    : 'View the sources that were used in the creation of this webpage',
                  content,
                },
              ]}
            />
          </div>
          <div className='col-md-6 my-auto'>
            <img src={BackImg} alt='back' />
          </div>
        </div>
      </div>
    </Container>
  )
}
